import './PersonalContact.scss';
import ButtonRequest from '../ButtonRequest/ButtonRequest';
import ChatManager from "../../assets/imgs/chat_manager_women.jpeg";
import ChatClient from "../../assets/imgs/chat_borrower.jpeg";

function PersonalContact({ handleRequestModal }) {
  return (
    <div className="personalContact container">
      <div className="personalContact__inner container__row">
        <div className="personalContact__chat">
          <img src={ChatManager} alt="" className="personalContact__chat__manager_img" />
          <div className="personalContact__chat__manager__first">,      </div>
          <div className="personalContact__chat__manager__second">    10  !</div>
          <div className="personalContact__chat__client">, !</div>
          <img src={ChatClient} alt="" className="personalContact__chat__client_img" />
        </div>
        <div className="personalContact__info">
          <h1 className="personalContact__info__title">   </h1>
          <div className="personalContact__info__subtitle">         !</div>
          <ButtonRequest handleRequestModal={handleRequestModal} />
        </div>
      </div>
    </div>
  )
}

export default PersonalContact;
