import './HowReceiveMoney.scss';
import Step from '../Step/Step';

function HowReceiveMoney () {
  return (
    <section className="container">
      <div className="howReceiveMoney container__row">
        <h1 className="howReceiveMoney__header">  </h1>
        <div className="howReceiveMoney__steps">
          <div className="howReceiveMoney__twoSteps">
            <Step page="market" text=" ,    " number="1" highlight={false}/>
            <Step page="market" text=" " number="2" highlight={true}/>
          </div>
          <div className="howReceiveMoney__twoSteps">
            <Step page="market" text="  " number="3" highlight={false}/>
            <Step page="market" text="    !" number="4" highlight={false}/>
          </div>
        </div>
      </div>
    </section>
  )
}

export default HowReceiveMoney;
